@use "global";

#boatbuilder-app main {
  display: grid;
  grid-template-columns: 10% 90%;
}

#boatbuilder-app .page {
  padding: 40px;

  header {
    text-align: center;
    padding: 20px;
    padding-top: 0px;
  }
}

#boatbuilder-app footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px 20px;
  background: #f7f7f7;
}

#boatbuilder-app .part-list.layout-rows {
  margin-bottom: 15px;
}

#boatbuilder-app .part-list.layout-box {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  column-gap: 20px;
  .part-container {
    font-size: 1.5rem;
    font-weight: 700;
    border: lightgray 1px solid;
    color: black;
    background: white;
    text-align: center;
    transition: all 0.3s;
    cursor: pointer;
    &.active,
    &:hover {
      border: var(--accent) 1px solid;
      background: var(--accent);
      color: white;
    }
    .part-label {
      display: block;
      padding: 10px 0;
    }
    .part-price {
      display: none;
    }
  }
}

#boatbuilder-app .part-list.layout-rows .part-container {
  padding: 6px 0;
  border-bottom: lightgray 1px solid;
  font-size: 1rem;
  .part-label {
    vertical-align: middle;
    padding-left: 10px;
  }
  .part-image-tooltip {
    vertical-align: middle;
    margin-left: 5px;
  }

  .part-price {
    float: right;
    font-weight: 700;
  }

  &:last-child {
    border: none;
  }

  .part-checkbox {
    display: inline-block;
    width: 30px;
    height: 30px;
    background: white;
    border-radius: 3px;
    margin: 2px;
    position: relative;
    transition: all 0.3s;
    border: 2px solid gray;
    vertical-align: middle;
    &:after {
      content: "";
      opacity: 0;
      position: absolute;
      border-left: 5px solid white;
      border-bottom: 5px solid white;
      width: 20px;
      height: 10px;
      transform: translateX(-50%) translateY(-50%) rotate(-45deg);
      top: 48%;
      left: 50%;
    }
  }

  .part-checkbox.active {
    background: var(--accent);
    border: 2px solid var(--accent);
    &:after {
      opacity: 1;
    }
  }
}

#boatbuilder-app .image-lightbox {
  display: none;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 50px;
  height: 100%;
  &.active {
    display: block;
  }
  .image-center {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 10;
    max-width: 90vh;
    max-height: 90vh;
  }
  .image-container {
    position: relative;
    z-index: 10;
    overflow: hidden;
    img {
      max-width: 100%;
      height: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }
  .image-lightbox-bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.75);
  }
  .image-lightbox-close-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
    background: white;
    height: 30px;
    width: 30px;
    z-index: 10;
  }
}

#boatbuilder-app .nav-buttons {
  text-align: right;
  a {
    margin: 5px;
    text-decoration: none;
    display: inline-block;
    background-color: white;
    line-height: 1;
    text-transform: uppercase;
    font-weight: 700;
    padding: 15px 30px;
    border: 2px solid black;
    font-family: var(--headings-font);
    transition: all 0.3s;

    &:hover {
      background-color: var(--accent);
      border-color: var(--accent);
      color: white;
    }
  }
}
